.header {
  display: flex;
  padding: 5px;
  background: rgba(255, 255, 255, 0.97);
  justify-content: center;
  box-shadow: 0 2px 2px -2px rgba(0, 0, 0, 0.15);
}
.headerMenu {
  text-align: center;
}
.headerMenu a {
  display: inline-block;
  margin-top: 0px;
  text-decoration: none;
  color: black;
  font-size: 35px;
  letter-spacing: 1.2px;
  font-family: "Quicksand", sans-serif;
  font-weight: bold;
}
.headerMenu a:hover {
  color: #5f9ea0;
}
