.author {
  width: 100%;
  margin-top: 10px;
  font-family: "Quicksand", sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: brown;
}
.authorName {
  float: left;
}
.blogDate {
  font-size: 14px;
  float: right;
  margin-top: 3px;
}
.symbol {
  font-size: 12px;
}
.featuredImage {
  width: 100%;
  height: 150px;
  object-fit: cover;
  background-color: black;
}
.blogMeta {
  padding-left: 10px;
  padding-right: 10px;
}
